
  main {
    margin-left: 22em;
    padding: 2em;

    @media (max-width: 780px) {
      margin-left: 0;
      padding: 1em;
    }

    .page > h1 {
      margin-top: 0;
    }
  }

  @media (min-width: 781px) {
    .mobile-only {
      display: none !important;
    }
  }

  @media (max-width: 780px) {
    .desktop-only {
      display: none !important;
    }
  }
